import React from "react";
import './ticket-item.css';


class HMTime {
  constructor(hours, minutes) {
    this.hours = hours
    this.minutes = minutes
  }
}


class TicketTime {

  constructor(from /*string TimeStamp*/, duration /* Minutes */) {
    this.from = this.parseTimeStamp(from)
    this.duration = this.parseMinutes(duration)

    this.calcTo()
  }

  parseTimeStamp(timeStampDate) {
    const date = new Date(timeStampDate)

    const minutes = date.getMinutes()
    const hours = date.getHours()

    return new HMTime(hours, minutes)
  }

  parseMinutes(minutes) {
    const hours = Math.floor(minutes / 60)
    const min = minutes % 60

    return new HMTime(hours, min)
  }

  addZero(value) {
    return (value < 10)
      ? '0' + value
      : value
  }

  getFrom() {
    const hours = this.addZero(this.from.hours)
    const minutes = this.addZero(this.from.minutes)

    return `${hours}:${minutes}`
  }

  calcTo() {
    let hours = this.from.hours + this.duration.hours

    if (hours >= 24) {
      hours -= 24
    }

    let minutes = this.from.minutes + this.duration.minutes

    if (minutes >= 60) {
      hours += 1

      minutes %= 60
    }

    this.to = new HMTime(hours, minutes)
  }

  getTo() {
    const hours = this.addZero(this.to.hours)
    const minutes = this.addZero(this.to.minutes)

    return `${hours}:${minutes}`
  }

  getDuration() {
    const hours = this.addZero(this.duration.hours)
    const minutes = this.addZero(this.duration.minutes)

    return `${hours}h ${minutes}m`
  }

}


export const TicketItem = ({price, carrier, segments}) => {

    const carrierIcon = 'https://pics.avs.io/99/36/' + carrier + '.png'

    const transfers = (stops) => {
      switch (stops) {
        case 0:
          return 'Без пересадок'

        case 1:
          return "1 пересадка"

        default:
          return stops + ' пересадки'
      }
    }


    return (
        <div className={'ticket-item'}>
            <div className="top-container">
              <div className={'price'}>
                {price + ' ₽'}
              </div>
              <div className={'carrier'}>
                <img
                  src={carrierIcon}
                  alt={'img'}/>
              </div>
            </div>

            <div className={'flight-description'}>
                {segments.map(i => {

                  const time = new TicketTime(i.date, i.duration)
                  const stopsCount = i.stops.length

                  return (
                    <div className={'way-line'}>

                      <div className="left-part">
                        <div className="origin-destination grey12">
                          <div>
                            {i.origin + " - "}

                            {i.destination}
                          </div>
                        </div>

                        <div>
                          {
                            time.getFrom() + ' -'
                          }

                          {
                            ' ' + time.getTo()
                          }
                        </div>
                      </div>

                      <div className={'middle-part'}>
                        <div className="grey12">
                          В пути
                        </div>
                        <div className={'flight-duration'}>
                          {
                            time.getDuration()
                          }
                        </div>
                      </div>

                      <div className={'right-part'}>
                        <div className={'grey12'}>
                          {
                            transfers(stopsCount)
                          }
                        </div>
                        {
                          i.stops.join(', ')
                        }
                      </div>
                    </div>
                  )
                })}
            </div>

        </div>
    )
}