import React from "react";
import './rate-options.css';
import {useSelector} from "react-redux";
import {ratesSelector} from "../redux/slices/transfer-slice";
import {RateOptionsItem} from "../rate-options-item/rate-options-item";


export const RateOptions = ({}) => {
  const optionsList = useSelector(ratesSelector)

    return (
        <div className={'rate-options'}>
          {
            optionsList.map(i => (
              <RateOptionsItem
                id={i.id}
                name={i.name}
                isChecked={i.isChecked}
              />
            ))
          }
        </div>
    )
}