import {createSlice} from "@reduxjs/toolkit";
import {
  checkedRatesStatusSelector,
  checkedStopsRadioButtonStatusSelector,
  RatesEnum,
  StopsEnum
} from "./transfer-slice";


const TicketCountOnPage = 5

export const ticketsSlice = createSlice({
  name: 'tickets-slice',

  initialState: {
    ticketsArr: [],   //сюда загружаются все билеты после setTickets
    filteredTickets: [],
    viewCount: TicketCountOnPage
  },

  reducers: {
    setTickets: (state, action) => {
      state.ticketsArr = action.payload.tickets
    },

    loadNextPageTickets: (state) => {
      state.viewCount += 5
    }
  }
})


/*
* Actions
* */
export const {
  setTickets,
  loadNextPageTickets
} = ticketsSlice.actions

/*
* Selectors
* */
export const ticketsSelector = (store) => {
  const tickets = store.tickets.ticketsArr

  if (tickets.length < 1) {
    return []
  }
                                                       // Пересадки

  const optionStatus = checkedStopsRadioButtonStatusSelector(store)

  const zeroStops = (length1, length2) => (                             // Только если в обоих инсдексах ранвное кол-во пересадок
    length1 === 0 && length2 === 0
  )

  const oneStop = (length1, length2) => (
    (length1 < 2 && length2 < 2)
    &&
    (length1 >= 0 && length2 >= 0)
  )

  const twoStops = (length1, length2) => (
    (length1 < 3 && length2 < 3)
    &&
    (length1 >= 0 && length2 >= 0)
  )

  let newArr = []

  switch (optionStatus) {
    case StopsEnum.All:

      newArr = store.tickets.ticketsArr

      break

    case StopsEnum.Nope:

      tickets.forEach((item, index) => {
        const length1 = item.segments[0].stops.length
        const length2 = item.segments[1].stops.length

        if (zeroStops(length1, length2)) {
          newArr.push(item)
        }
      })

      break

    case StopsEnum.One:

      tickets.forEach((item, index) => {
        const length1 = item.segments[0].stops.length
        const length2 = item.segments[1].stops.length

        if (oneStop(length1, length2)) {
          newArr.push(item)
        }
      })

      break

    case StopsEnum.Two:

      tickets.forEach((item, index) => {
        const length1 = item.segments[0].stops.length
        const length2 = item.segments[1].stops.length

        if (twoStops(length1, length2)) {
          newArr.push(item)
        }
      })

      break

    default:
      break
  }

                                                      // Самый дешевый и самый быстрый билеты
  let ratesStatus = checkedRatesStatusSelector(store)

  switch (ratesStatus) {

    case RatesEnum.Cheapest:
      const sorting = (arr) => {
        const len = arr.length;

        for (let i = 0; i < len; i++) {
          for (let j = 0; j < len - i - 1; j++) {
            if (arr[j].price - 0 > arr[j + 1].price - 0) {    // меняем местами
              let temp = arr[j];
              arr[j] = arr[j + 1];
              arr[j + 1] = temp;
            }
          }
        }
        return arr;
      }
      let copyArr = [...newArr]
      newArr = sorting(copyArr)

      return [newArr[0]]

      break

    case RatesEnum.Fastest:
      const sortingFastets = (arr) => {
        const len = arr.length;
        for (let i = 0; i < len; i++) {
          for (let j = 0; j < len - i - 1; j++) {
            const firstDuration = (arr[j].segments[0].duration - 0) + (arr[j].segments[1].duration - 0)
            const secondDuration = (arr[j + 1].segments[0].duration - 0) + (arr[j + 1].segments[1].duration - 0)

            if (firstDuration > secondDuration) {              // меняем местами
              let temp = arr[j];
              arr[j] = arr[j + 1];
              arr[j + 1] = temp;
            }
          }
        }
        return arr;
      }
      let copyArr2 = [...newArr]
      newArr = sortingFastets(copyArr2)

      return [newArr[0]]

      break
  }

  return newArr.slice(0, store.tickets.viewCount)
}


/*
* Reducer
* */
export const ticketsReducer = ticketsSlice.reducer