import React from "react";
import './left-pannel.css';
import {CheckboxItem} from "../checkbox-item/checkbox-item";
import {useSelector} from "react-redux";
import {optionsSelector} from "../redux/slices/transfer-slice";


export const LeftPannel = ({}) => {
  const optionList = useSelector(optionsSelector)

    return (
        <div className={'left-pannel'}>
            <h1 className={'options-header'}>
              Количество пересадок
            </h1>

          <div className={'transfers'}>
            {
              optionList.map(i => (
                <CheckboxItem
                  id={i.id}
                  isChecked={i.isChecked}
                  name={i.name}
                />
              ))
            }
          </div>
        </div>
    )
}