import React from "react";
import './checkbox-item.css';
import FormIcon from '../../attachments/Form.svg';
import CheckedIcon from '../../attachments/Checkbox.svg'
import {useDispatch} from "react-redux";
import {changingIsClicked} from "../redux/slices/transfer-slice";


export const CheckboxItem = ({id, isChecked, name}) => {
  const dispatch = useDispatch()

  const onCheck = (id) => {
    const payload = {
      id: id
    }
    dispatch(changingIsClicked(payload))
  }

    return (
        <div
          className={'checkbox-item'}
          onClick={() => onCheck(id)}
        >
          <div className={'box'}>
            {
              (isChecked)
                ? <img src={CheckedIcon}
                       alt="" />
                : <img src={FormIcon}
                       alt="" />
            }
          </div>

          <div className={'box-text'}>
            {name}
          </div>
        </div>
    )
}


