import React, {useEffect} from "react";
import './tickets.css';
import {TicketItem} from "../ticket-item/ticket-item";
import {useDispatch, useSelector} from "react-redux";
import {loadNextPageTickets, setTickets, ticketsSelector} from "../redux/slices/tickets-slice";


export const Tickets = ({}) => {
  const dispatch = useDispatch()

  useEffect(() => {
    fetch('https://front-test.beta.aviasales.ru/search')
      .then(res => res.json())
      .then(data => {
        const url = 'https://front-test.beta.aviasales.ru/tickets?searchId=' + data.searchId

        fetch(url)
          .then(res => res.json())
          .then(ticketsData => {
            const tickets = ticketsData.tickets

            const payload = {
              tickets: tickets
            }
            dispatch(setTickets(payload))
          })
      })

  }, [])


  const moreTickets = () => {
    dispatch(loadNextPageTickets())
  }

  const tickets = useSelector(ticketsSelector)

  return (
    <div className={'tickets'}>
      {
        tickets.map( i => (
          <TicketItem
              price={i.price}
              carrier={i.carrier}
              segments={i.segments}
            />)
        )
      }
      <div className={'more-btn'}>
        <button
          className={'btn'}
          onClick={moreTickets}
        >
          Показать еще 5 билетов
        </button>
      </div>
    </div>
  )
}







