import React from "react";
import './rate-options-item.css';
import {useDispatch} from "react-redux";
import {getCheapest} from "../redux/slices/transfer-slice";


export const RateOptionsItem = ({id, name, isChecked}) => {
    const dispatch = useDispatch()

    const onCheck = (id) => {
        const payload = {
            id: id
        }
        dispatch(getCheapest(payload))
    }

    return (
        <div
          className={isChecked ? 'rate-options-item blue-background' : 'rate-options-item'}
          onClick={() => onCheck(id)}>
            {name}
        </div>
    )
}