import {createSlice} from "@reduxjs/toolkit";

export const StopsEnum = {
  All: 'all',
  Nope: 'nope',
  One: 'one',
  Two: 'two',
}

export const RatesEnum = {
  Nope: 'nope',
  Cheapest: 'cheapest',
  Fastest: 'fastest'
}

export const transferSlice = createSlice( {
  name: 'transfer',
  initialState: {
    options: [
      {
        id: 1,
        name: 'Все',
        isChecked: true,
        enumStatus: StopsEnum.All
      },
      {
        id: 2,
        name: 'Без пересадок',
        isChecked: false,
        enumStatus: StopsEnum.Nope
      },
      {
        id: 3,
        name: '1 пересадка',
        isChecked: false,
        enumStatus: StopsEnum.One
      },
      {
        id: 4,
        name: '2 пересадки',
        isChecked: false,
        enumStatus: StopsEnum.Two
      },
      {
        id: 5,
        name: '3 пересадки',
        isChecked: false,
        enumStatus: StopsEnum.All
      }
    ],
    rates: [
      {
        id: 1,
        name: 'Самый дешевый',
        isChecked: false,
        enumStatus: RatesEnum.Cheapest
      },
      {
        id: 2,
        name: 'Самый быстрый',
        isChecked: false,
        enumStatus: RatesEnum.Fastest
      },
    ],
    testFetch: []
  },
  reducers: {
    changingIsClicked: (state, action) => {
      const id = action.payload.id
      const item = state.options.find(i => i.id === id)

      const arr = state.options.map(i => i.isChecked) // [false, false, false, false, false]

      item.isChecked = !item.isChecked
      for (let i = 0; i<arr.length; i++) {
        if (arr[i] === true) {
          state.options[i].isChecked = false
        }
      }
    },
    getCheapest: (state, action) => {
      const id = action.payload.id
      const item = state.rates.find(i => i.id === id)
      const arr = state.rates.map(i => i.isChecked)

      item.isChecked = !item.isChecked

      for (let i = 0; i<arr.length; i++) {
        if (arr[i] === true) {
          state.rates[i].isChecked = false
        }
      }
    },
  }
})


/*
* Actions
* */
export const {
  changingIsClicked,
  getCheapest,

} = transferSlice.actions

/*
* Selectors
* */
export const optionsSelector = (store) => store.transfer.options
export const ratesSelector = (store) => store.transfer.rates

export const checkedStopsRadioButtonStatusSelector = (store) => store.transfer.options.find(i => i.isChecked).enumStatus
export const checkedRatesStatusSelector = (store) => {
  const rate = store.transfer.rates.find(i => i.isChecked)

  return (rate !== undefined) ? rate.enumStatus : RatesEnum.Nope
}

/*
* Reducer
* */

export const transferReducer = transferSlice.reducer